import Vue from 'vue'
import Router from 'vue-router'

import index from '../pages/index.vue'
import list from '../pages/list.vue'
import permission from '../pages/permission.vue'
import publishMes from '../pages/publishMes.vue'
import listDetail from '../pages/listDetail.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
	    path: '/',
	    name: 'index',
	    component: index
    },
    {
    	path:'/index',
    	component:index
    },
    {
    	path:'/list',
    	name:'list',
    	component:list
    },
    {
        path:'/listDetail/:urlControl',
        component:listDetail
    },
    {
    	path:'/permission',
    	name:'permission',
    	component:permission
    },
    {
    	path:'/publishMes',
    	name:'publishMes',
    	component:publishMes
    }
  ]
})



