<template>
	<div class="initialized">
		<div class="container" id='home'>
			<h4>企业内部管理系统，为企业内部通信提供最满意的服务！</h4>
			<div class="nav">
				<ul>
					<li><a href="#/index">首页</a></li>
					<li><a href="#/list">消息列表</a></li>
					<li><a href="#/publishMes">发布新消息</a></li>
					<li class="active"><a href="#">身份识别</a></li>
				</ul>
			</div>
			<div class="cur-location">
				当前位置：<a href="#/index">[首页]</a> - <a href="#">[员工身份识别]</a>
			</div>
			<div class="permis_group" v-show='!userStatus'>				
				<div class="permis-dialog">
					<form id='per_login'>				
						<p><span>员工身份识别</span></p>
						<div class="ygbh"><label>员工编号：</label><input type="text" placeholder="请输入员工编号"></div>
						<div class="xtkl"><label>系统口令：</label><input type="password" placeholder="请输入系统口令"></div>
						<div class="btn-group">
							<button @click='login'>提 交</button>
							<button @click='reset_form'>重 置</button>
						</div>
					</form>
				</div>
			</div>
			<div class="show_has_login" v-if='userStatus'>
				<h2>您已成功登录! <button class="permis_quit_btn" @click='quit'>点击退出登录</button></h2>
			</div>
		</div>
		<div class="footer">
			<p>企业日常事务管理系统&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>


<script>
	import $ from 'jquery';
	export default {
		name:'index',
		data:function() {
			return {
				userStatus:false,
				loginStatus:false,
				users:[],
				curUser:[]
			}
		},
		mounted(){
			var _this= this
				_this.initData()
				var curName = window.localStorage.getItem('name')
				if(curName){
					_this.userStatus = true
				}
				var storage = window.localStorage;
		    	var curName = storage.getItem('name'),
		    		curSex = storage.getItem('sex'),
		    		curLeadership = storage.getItem('leadership'),
		    		curId = storage.getItem('id'),
		    		curBirthdate = storage.getItem('birthdate'),
		    		curTel = storage.getItem('tel'),
		    		curAddress = storage.getItem('address')
		    	if(curLeadership){
			    	if( curLeadership == 'true'){
			    		curLeadership = '是'
			    	}else{
			    		curLeadership =  '否'
			    	}
		    	}
		    	var _this= this;	    	
		    	if(curName || curSex){
		    		_this.userStatus = true
		    		_this.curUser.push({
		    			name:curName,
		    			sex:curSex,
		    			leadership:curLeadership,
		    			id:curId,
		    			birthdate:curBirthdate,
		    			tel:curTel,
		    			address:curAddress
		    		})
		    	}
		},
		methods:{
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.users = res.data.users;
				})
			},
			/*清除localStorage*/
			quit(){
				window.localStorage.removeItem('id')
				window.localStorage.removeItem('name')
				window.localStorage.removeItem('sex')
				window.localStorage.removeItem('birthdate')
				window.localStorage.removeItem('tel')
				window.localStorage.removeItem('address')
				window.localStorage.removeItem('leadership')
				alert('退出成功!')
				this.userStatus = false;
			},
	        //登录
	        login: function() {
	            var username = $(".ygbh").find("input").val().trim(); //获取用户名
	            var psw = $(".xtkl").find("input").val(); //获取密码
	            var flag = false;
	            var _this = this;
	            for (var i = 0, len = _this.users.length; i < len; i++) {
	                //判断用户名密码是否正确
	                if (_this.users[i].username === username && _this.users[i].password === psw) {
	                	flag = true;/*
	                    _this.curUser={
	                    	name:_this.users[i].name,
	                    	sex:_this.users[i].sex
	                    }	*/ 
	                    var storage = window.localStorage;
	                    storage.setItem('name',_this.users[i].name)
	                    storage.setItem('sex',_this.users[i].sex)
	                   	storage.setItem('id',_this.users[i].id)
	                    storage.setItem('leadership',_this.users[i].leadership)   
	                    storage.setItem('birthdate',_this.users[i].birthdate)
	                    storage.setItem('tel',_this.users[i].tel)
	                    storage.setItem('address',_this.users[i].address)

	                	alert("登录成功！您好：" + _this.users[i].name);
	                	$("#per_login")[0].reset();	                	
	                	_this.$router.push('/index');
	                }
	            }
	            if (!flag) {
	            	alert("输入的账号或密码不正确！");
	            	$("#per_login")[0].reset();
	            	$(".ygbh").find("input").focus()
	            }
	        },
	        reset_form(){
	        	$('#per_login')[0].reset();
	        	$(".ygbh").find("input").focus()
	        }
	    }
	}
</script>

<style>
	@import '../css/main.css';
	.show_has_login{
		width: 100%;
		height: 330px;
		text-align: center;
		padding-top: 100px;
	}
	.permis_quit_btn{
		border:none;
		outline: none;
		background: transparent;
		font-size: 20px;
		cursor: pointer;
		transition:0.3s;
		font-weight: bold;
		color: rgb(31,122,193);
	}
	.permis_quit_btn:hover{
		color:red;
	}
	.permis_group{
		height: 300px;
	}
</style>


