export default {
	formatTime(date){
		let dateTime='';
		let month = date.getMonth(),
			months = month +1;
		let curTime = date.getFullYear() +'-'+months +'-'+date.getDate()+' '+date.getHours()+':'+date.getMinutes()+':'+date.getSeconds();
		dateTime = curTime;
		return dateTime;
	}
}


