<template>
	<div class="initialized">
		<div class="container" id='home'>
			<h4>企业内部管理系统，为企业内部通信提供最满意的服务！</h4>
			<div class="nav">
				<ul>
					<li><a href="#/index">首页</a></li>
					<li class="active"><a href="#">消息列表</a></li>
					<li><a href="#/publishMes">发布新消息</a></li>
					<li><a href="#/permission">身份识别</a></li>
				</ul>
			</div>
			<div class="cur-location">
				当前位置：<a href="#/index">[首页]</a> - <a href="#/list">[消息列表]</a>
			</div>
			<div class="content-list-item">
				<ul>
					<li v-for='i in users'>						
						<router-link v-bind:to="'/listDetail/'+i.urlControl">
							<div class="list-item">
								<span class="item-words">&gt;&gt;{{i.topic}}</span>
								<span class="publish-info">发布人：{{i.name}} 发布时间：{{i.time}}</span>
							</div>
						</router-link>
					</li>
				</ul>
				<div class="page-tab">
					<ul>
						<li><a href="#" @click='prevPage'>首页</a></li>
						<li><a href="#" @click='prevPage'>上一页</a></li>
						<li><a href="#" @click='nextPage'>下一页</a></li>
						<li><a href="#">尾页</a></li>
						<li><a href="#">当前为第一页</a></li>
						<li><a href="#">共两页</a></li>
					</ul>
				</div>
			</div>
		</div>
		<div class="footer">
			<p>企业日常事务管理系统&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>



<script>
	export default {
		name:'list',
		data:function() {
			return {
				userStatus:false,
				users:[],
				usersCache:[],
				curUser:[]
			}
		},
		mounted(){
			this.$nextTick(function(){
				this.initData()
			})
		},
		methods:{
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.usersCache=res.body.users;
					this.users=this.usersCache.slice(0,6);
					var storage = window.localStorage;
					var pubMesTopic = storage.getItem('pubMesTopic'),
						pubMesContent = storage.getItem('pubMesContent'),
						curName = storage.getItem('name'),
						curTime = storage.getItem('time')
					if(pubMesTopic){
						this.users.unshift({
							topic:pubMesTopic,
							name:curName,
							time:curTime
						})
						this.users.pop()
					}
				})
			},
			nextPage(){
				this.users = this.usersCache.slice(6,12);
			},
			prevPage(){
				this.users = this.usersCache.slice(0,6);
			}
	    }
	}
</script>

<style>
	@import '../css/main.css';
	.list-item{
		height: 40px;
	}
	.item-words{
		display: inline-block;
		font-style: normal;
		width: 1000px;
		height: 40px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		color: #111;
	}
</style>


