<template>
	<div class="initialized">
		<div class="container" id='home'>
			<h4>企业内部管理系统，为企业内部通信提供最满意的服务！</h4>
			<div class="nav">
				<ul>
					<li><a href="#/index">首页</a></li>
					<li class="active"><a href="#">消息列表</a></li>
					<li><a href="#/publishMes">发布新消息</a></li>
					<li><a href="#/permission">身份识别</a></li>
				</ul>
			</div>
			<div class="cur-location">
				当前位置：<a href="#/index">[首页]</a> - <a href="#/list">[消息列表]</a>- <a href='#'>[查看具体消息]</a>
			</div>
			<div class="item-content">
				<div class="item-content-topic">
					<h2>{{curUser.topic}}</h2>
				</div>
				<div class="item-content-main">
					{{curUser.words}}
					<i>发布人：{{curUser.name}} 发布时间：{{curUser.time}}</i>
				</div>
				<div class="colleague-reply">					
					<div class="leader-reply" v-show='!leadReplyContent==""'>
						领导批复：<br><span>{{leadReplyContent}}</span>
					</div>
					<div v-if='!colleagueReplyContent==""'>
						回复：<br><span class="col-reply">{{colleagueReplyContent}}</span>
					</div>
					<!-- 插件 -->
					<div class="table"> 
						<editor id="editor_id" height="500px" width="700px" :content.sync="editorText"
						            pluginsPath="../../../static/kindeditor/plugins/"
						            :loadStyleMode="false"
						            @on-content-change="onContentChange">
						</editor>
	 				</div>	 							
					<div class="pub-btn-group">
						<button @click='colleagueReply'>回复</button>
						<button @click='leaderReply'>领导批复</button>
					</div>
					<!-- 插件 -->
				</div>
			</div>
		</div>
		<div class="footer">
			<p>企业日常事务管理系统&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>



<script>
	export default {
		name:'list',
		data:function() {
			return {
				editorText: '', // 双向同步的变量
     			editorTextCopy: '' , // content-change 事件回掉改变的对象
				userStatus:false,
				loginStatus:false,
				users:[],
				curUser:[],
				urlControl:this.$route.params.urlControl,
				leadReplyContent:'',
				colleagueReplyContent:''
			}
		},
		mounted(){
			this.$nextTick(function(){
				var _this = this;
				_this.initData()

			})
		},
		methods:{
			initData(){
				var _this= this
				this.$http.get('../../static/userInfo.json').then((res)=>{
					_this.users=res.data.users;
					_this.users.forEach(function(value,index){
						if(value.urlControl == _this.urlControl){
							_this.curUser = {
								name: _this.users[index].name,
								topic: _this.users[index].topic,
								words:_this.users[index].words,
								time:_this.users[index].time
							}
						}
					})	


				})
			},
			onContentChange (val) {
		      this.editorTextCopy = val;
		    },
			leaderReply(){
				var _this = this;
				var isLeader = window.localStorage.getItem('leadership');
				if(!isLeader){	
						if(confirm('请先登录，点击确定登录')){
							_this.$router.push('/permission')
						}			
					}else if(isLeader == "false"){
						alert('抱歉，您没有权限')
							}else{
						_this.leadReplyContent = _this.editorTextCopy;
						}
			},
			colleagueReply(){
				var curName = window.localStorage.getItem('name');
				var _this = this;
				if(!curName){
					if(confirm('请先登录，点击确定登录')){
						_this.$router.push('/permission');
					}
				}else{
					_this.colleagueReplyContent = _this.editorTextCopy;
				}
			}
	    }
	}
</script>

<style>
	@import '../css/main.css';
	.item-content{
		width: 100%;
		height: 330px;
		border:solid rgb(22, 113, 180) 1px;
		border-radius: 3px;
		margin-bottom: 60px;
		overflow: auto;
	}
	.item-content-topic{
		width: 100%;
		height: 40px;
		line-height: 40px;
		text-align: center;
	}
	.item-content-main{
		padding-left: 30px;
	}
	.item-content-main i {
		font-size:14px;
		float: right;
		padding: 10px 10px 0 0; 
	}
	.leader-reply{
		margin: 15px;
	}
	.colleague-reply{
		width: 100%;
		height: 200px;
		padding-left: 25px;
	}
	.leader-reply>span{
		color: red;
	}
	.col-reply{
		color: red;
	}
</style>


