<template>
	<div class="initialized">
		<div class="container" id='home'>
			<div class="userinfos" v-show='userStatus'>
			</div>
			<h4>企业内部管理系统，为企业内部通信提供最满意的服务！</h4>
			<div class="cur_user_info" v-if='userStatus'>当前用户：{{curUser[0].name}}</div>
			<div class="nav">
				<ul>
					<li><a href="#/index">首页</a></li>
					<li><a href="#/list">消息列表</a></li>
					<li class="active"><a href="#">发布新消息</a></li>
					<li><a href="#/permission">身份识别</a></li>
				</ul>
			</div>
			<div class="cur-location">
				当前位置：<a href="#/index">[首页]</a> - <a href="#">[发布新消息]</a>
			</div>
			<!-- 登录框 -->
			<div class="loginbox" v-show="loginStatus" @click="loginboxClick">
		        <div class="box" @click="stopProp()">
		            <h3>用户登录</h3>
		            <form name="login" id="login">
		                <input type="text" placeholder="请输入员工编号" class="username" :class='{hasFocus:judgeFocus == 1}' @click='judgeFocus = 1' @blur='judgeFocus = 0'>
		                <input type="password" placeholder="请输入系统口令" class="psw" :class='{hasFocus:judgeFocus == 2}' @click='judgeFocus = 2'  @blur='judgeFocus = 0'>
		                <input type="button" value="立即登录" @click="login">
		            </form>
		        </div>
	        </div>
	        <!-- 登录框结束 -->
	        <!-- 富文本编辑器 -->
			<div class="publishMes">
				<p class="notice" v-show='!userStatus'>*要发布消息必须先进行身份识别</p>
				<form>
					<label>消息标题：</label><input type="text" class="mesTitle" placeholder="请输入要发布的消息标题"><br>
					<label>消息内容：</label>
					<!-- 插件 -->
					<div class="table"> 
					    <editor id="editor_id" height="500px" width="700px" :content.sync="editorText"
					            pluginsPath="../../../static/kindeditor/plugins/"
					            :loadStyleMode="false"
					            @on-content-change="onContentChange">
					    </editor>
 					 </div>
					<!-- 插件 -->
				</form>			
				<div class="pub-btn-group">
					<button @click='publish_mes'>提 交</button>
					<button @click='loginStatus = true' v-if='!userStatus'>登 录</button>
				</div>
			</div>		
			<!-- 富文本编辑器结束 -->
		</div>
		<div class="footer">
			<p>企业日常事务管理系统&nbsp;&nbsp;<i class="icon-phone"></i>18271191060</p>
		</div>
	</div>
</template>
<script>
	import $ from 'jquery';
	export default {
		name:'index',
		data:function() {
			return {
				editorText: '', // 双向同步的变量
     			editorTextCopy: '' , // content-change 事件回掉改变的对象
				userStatus:false,
				loginStatus:false,
				users:[],
				curUser:[],
				isfocus:false,
				judgeFocus:0
			}
		},
		mounted(){
			this.$nextTick(function(){
				var _this= this
				_this.initData()
				var curName = window.localStorage.getItem('name')
				if(curName){
					_this.userStatus = true
				}
				var storage = window.localStorage;
		    	var curName = storage.getItem('name');
		    	var _this= this;	    	
		    	if(curName){
		    		_this.userStatus = true
		    		_this.curUser.push({
		    			name:curName
		    		})
		    	}
			})
		},
		methods:{
			initData(){
				this.$http.get('../../static/userInfo.json').then((res)=>{
					this.users = res.data.users;
				})
			},
			onContentChange (val) {
		      this.editorTextCopy = val;
		    },
			publish_mes:function(e){
				var _this = this
				e.preventDefault()
				if(_this.userStatus == false){
					if(confirm('您还未登录，点击确定登录')){
						_this.loginStatus=true
					}	
				}else{
					if(_this.editorTextCopy.trim() == ''){
						alert('您还未输入内容')
					}else{
						var pubTopic = $('.mesTitle').val();
						var date = new Date();
						var curTime = _this.$formatTime.formatTime(date)
						var storage = window.localStorage;
						storage.setItem('pubMesTopic',pubTopic);
						storage.setItem('pubMesContent',_this.editorTextCopy);
	                    storage.setItem('time',curTime);
						alert('提交成功');
					}
				}
			},
			stopProp:function(e){
				e= e || event;
				e.stopPropagation()
			},
			loginboxClick:function(){
				this.loginStatus=false
			},
	        //登录
	        login: function() {
	        	var _this = this
	            var username = $(".loginbox").find(".username").val(); //获取用户名
	            var psw = $(".loginbox").find(".psw").val() //获取密码
	            var flag = false;
	            for (var i = 0, len = _this.users.length; i < len; i++) {
	                //判断用户名密码是否正确
	                if (_this.users[i].username === username && _this.users[i].password === psw) {
	                	flag = true;
	                	alert("登录成功！您好："+_this.users[i].name);
	                	$("#login")[0].reset();
	                    //用户登录框消失
	                    _this.loginStatus = false;
	                    //设置登录状态
	                    _this.userStatus = true;
	                    /*设置localStorage*/
	                   	var storage = window.localStorage;
	                    storage.setItem('name',_this.users[i].name)
	                    storage.setItem('sex',_this.users[i].sex)
	                   	storage.setItem('id',_this.users[i].id)
	                    storage.setItem('leadership',_this.users[i].leadership)   
	                    storage.setItem('birthdate',_this.users[i].birthdate)
	                    storage.setItem('tel',_this.users[i].tel)
	                    storage.setItem('address',_this.users[i].address)
	                    break;
	                    _this.editorText = '哈哈'
	                }
	            }
	            var curName = window.localStorage.getItem('name')
	            _this.curUser.push({
	            	name:curName
	            })
	            if (!flag) {
	            	alert("输入的账号或密码不正确！");
	            	$("#login")[0].reset();
	            }
	        }
	    }
	}
</script>


<style>

@import '../css/main.css';
.table,.kindeditor{
	width: 1200px;
	height: 230px;
}
.ke-container{
	width: 1200px !important;
	height: 230px;
}
.pub-btn-group{
	margin:0;
}
.pub-btn-group button{
	position: relative;
	top:-30px;
	width: 60px;
	height: 30px;
	line-height: 30px;
	cursor: pointer;
	background: rgb(22, 123, 180);
	border: none;
	outline: none;
	margin-right: 5px;
	color: #fff;
	border-radius: 3px;
}
.pub-btn-group button:hover{
	background: rgb(0, 106, 161);
}
.hasFocus{
	border: solid 2px rgb(22,123,180) !important;
}
</style>


